.selectBlog-page{
    position: absolute;
    width: 100%;
    height: 100%;
    background: #f3f3f4;
}
.selectBolg-left{
    background: white;
    padding-bottom: 15px;
}
.selectBolg-left h2{
    padding-left: 10px;
    padding-top: 15px;
    padding-bottom: 15px;
    border: 1px solid #e7eaec;
}
.selectBolg-left ul{
    padding: 0;
    padding-top: 15px;
}
.selectBolg-left ul li{
    list-style: none;
}
.selectBolg-left ul li a{
    display: block;
    padding: 10px;
    font-size: 20px;
    background-color: #fff5ee;
    border-top: 1px solid #f8d4bc;
    border-bottom: 1px solid #f8d4bc;
    color: #f60;
    text-align: center;
}